import { handleResponse,checkRoleAutorizationFail, authHeader } from "./midlwares";
import {SERVEUR_URL} from '../config/config'
const USER_API=SERVEUR_URL+'/api/user';

/**
 * Register a new client.
 * @param {*} data 
 */
const registerCient = (data) => {
    
    const params = [
        "username",
        "password",
        "wilaya",
        "commune",
        "phone"
        ];
    const keys=Object.keys(data);
    if (!params.every((key) => keys.includes(key)))
        return Promise.reject({
            message: "Les param√®tres de formulaire sont invalides",
        });
     
    const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json"},
        body: JSON.stringify(data)
    };

  return fetch(`${USER_API}/clients/signup`, requestOptions).then(
    handleResponse
  );
};



/**
 * Subscribe client for a new abonnement.
 * @param {*} data 
 */
const createAbonnement = (data) => {
    if (checkRoleAutorizationFail(data))
    return Promise.reject({
    message:
        "Non autoris√© pour effectuer cette t√¢che",
    });
    
    const params = [
        "userType",
        "code",
        "type",
        "duree",
        "deviceDetect",
        "classesIds"
        ];
    const keys=Object.keys(data);
    if (!params.every((key) => keys.includes(key)))
        return Promise.reject({
            message: "Les param√®tres de formulaire sont invalides",
        });
     
    const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json",...authHeader() },
        body: JSON.stringify(data)
    };

  return fetch(`${USER_API}/abonnements/new`, requestOptions).then(
    handleResponse
  );
};


